.field__input {
  width: 100%;
  min-height: 60px;
  font-family: inherit;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: $color-primary;
  border: $border-form;
  border-radius: $border-form-radius;
}

.field__label {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: $color-accent;
  text-transform: uppercase;

  display: block;
  margin-top: 20px;
  margin-bottom: 5px;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    padding-left: 0;
    margin: 0 20px 0 0;
  }
}

.fiel-label--lastname {
  margin-top: 0;
  margin-bottom: 5px;
}

.field__input--textarea {
  @media (min-width: $tablet-width) {
    height: 190px;
  }

  @media (min-width: $desktop-width) {
    height: 124px;
  }
}
